import { Client } from "./client";
import { ref } from "vue";

const globalPreference = ref<"full_name" | "preferred_name">("full_name");
let hasInitialized = false;

export function useTestNamePreference() {
  /**
   * Get the user's test name display preference
   * @returns "full_name" or "preferred_name"
   */
  const getPreference = (): string => {
    if (hasInitialized) {
      return globalPreference.value;
    }

    const client = Client();
    const userData = client.token?.value?.authorization?.user;
    if (userData && userData.preferences) {
      const testNamePref = userData.preferences.find(
        (p: any) => p.name === "test_name_display" && p.target === "site"
      );
      if (testNamePref) {
        globalPreference.value = testNamePref.value;
        hasInitialized = true;
        return globalPreference.value;
      }
    }

    globalPreference.value = "full_name";
    hasInitialized = true;
    return globalPreference.value;
  };

  /**
   * Generate a preferred name for a test if one doesn't exist
   * @param testTypeName The full test type name
   * @returns The generated preferred name
   */
  const generatePreferredName = (testTypeName: string): string => {
    if (!testTypeName) return "";

    const words = testTypeName.split(' ');
    if (words.length > 1) {
      return words
        .map((word: string) => word.charAt(0))
        .join('')
        .toUpperCase();
    } else {
      return testTypeName.substring(0, 4).toUpperCase();
    }
  };

  /**
   * Get the appropriate test name to display based on preferences
   * @param fullName The full test type name
   * @param preferredName The preferred name (if exists)
   * @returns The name to display based on user preferences
   */
  const getDisplayName = (fullName: string, preferredName?: string): string => {
    const preference = getPreference();
    if (preference === "full_name") {
      return fullName;
    }
    // If preferred_name is requested
    if (preferredName) {
      return preferredName;
    }
    // Generate preferred name if it doesn't exist
    if (fullName) {
      // return generatePreferredName(fullName);
      return fullName
    }
    return fullName;
  };

  return {
    getPreference,
    generatePreferredName,
    getDisplayName,
    preference: globalPreference
  };
}
