<template>
  <div>
    <TouchForm
      :fields="fields"
      :onFinish="onFinish"
      :skipSummary="true"
      :onFinishDisabled="isFinishDisabled"
      cancelDestinationPath="/home"
    />
    <div id="rootContainer"></div>
  </div>
</template>

<script setup lang="ts">
import Validation from "@/components/Forms/validations/StandardValidations";
import TouchForm from "@/components/Forms/TouchScreenForm.vue";
import { FieldType } from "@/components/Forms/BaseFormElements";
import { Field, Option } from "@/components/Forms/FieldInterface";
import { Client, ClientError } from "@/composables/client";
import { groupByTag } from "@/utils/Form";
import { useRoute } from "vue-router";
import { toastDanger, toastSuccess, toastWarning } from "@/utils/Alerts";
import PrintService from "@/services/print_service";
import { useTestNamePreference } from "@/composables/testNamePreference";
import { ref } from "vue";
import { PatientDialog } from "@/utils/PatientDialog";
import router from "@/router";
import { LabLocation } from "types";
import HisDate from "@/utils/Date";

const details = ref<any>();
const isFinishDisabled = ref<boolean>(false);
const modalOpen = ref<boolean>(false);

const handleModal = () => {
  modalOpen.value = true;
  PatientDialog(modalOpen.value, details.value);
};

const route = useRoute();
const client = Client();

const visitTypeField = () => {
  const options: Option[] = [];
  return {
    id: "visit_type",
    helpText: "Visit type",
    type: FieldType.TT_SELECT,
    requireNext: false,
    validation: (val: Option) => Validation.required(val),
    computedValue: (val: Option) => {
      return {
        encounter: {
          encounter_type: val.value,
        },
      };
    },
    init: async () => {
      const res = await client.getJson("encounter_types");
      if (!res?.ok) return false;
      res.data?.data.forEach((type: any) => {
        options.push({
          label: type.name,
          value: type.id,
        });
      });
      return true;
    },
    options: () => options,
  };
};

const requestingClinicianField = () => {
  return {
    id: "clinician",
    helpText: "Requesting Clinician",
    type: FieldType.TT_TEXT,
    computedValue: (val: Option) => {
      return {
        order: {
          requested_by: val.value,
        },
      };
    },
    options: async (f: any) => {
      if (f.clinician?.value) {
        const res = await client.getJson(
          "orders/search_by_requesting_clinician",
          {
            name: f.clinician?.value,
          }
        );
        return res?.data.map((v: string) => ({ label: v, value: v }));
      }
      return [];
    },
    validation: (val: Option) => Validation.required(val),
  };
};

const collectedByField = () => {
  return {
    id: "collector",
    helpText: "Sample collected by (Optional)",
    type: FieldType.TT_TEXT,
    computedValue: (val: Option) => {
      return {
        order: {
          collected_by: val?.value || "",
        },
      };
    },
  };
};

const requestingLocationField = () => {
  let sendingFacilityID: null | number = null;
  return {
    id: "ward",
    helpText: "Requesting ward/location",
    requireNext: false,
    type: FieldType.TT_SELECT,
    init: async () => {
      const global: any = await client.getJson("global");
      if (global.ok) {
        sendingFacilityID = global.data.id;
      }
      return true;
    },
    config: {
      showKeyboard: true,
    },
    computedValue: (val: Option) => {
      return {
        encounter: {
          facility_section: val.value,
          sending_facility: sendingFacilityID || -1,
        },
      };
    },
    validation: (val: Option) => Validation.required(val),
    options: async (f: any) => {
      const res: any = await client.getJson(
        "encounter_type_facility_section_mappings/facility_sections",
        {
          encounter_type_id: f.visit_type.value,
        }
      );
      return (res.data || []).map((data: any) => {
        return {
          label: data.name,
          value: data.id,
        };
      });
    },
  };
};

const dateField = () => {
  return {
    id: "date",
    helpText: "Date",
    type: FieldType.TT_DATE_PICKER,
    config: {
      maxDate: () => HisDate.currentDate()
    },
    validation: (val: Option) => {
      const requiredValidation = Validation.required(val);
      if (requiredValidation) return requiredValidation;

      const selectedDate = new Date(val.value);
      const today = new Date(HisDate.currentDate());

      selectedDate.setHours(0, 0, 0, 0);
      today.setHours(0, 0, 0, 0);

      if (selectedDate > today) {
        return ['Order date cannot be greater than today'];
      }

      return null;
    },
  };
};

const specimenTypeField = () => {
  let options: Option[] = [];
  const { getDisplayName } = useTestNamePreference();
  return {
    id: "specimen_type",
    helpText: "Specimen type",
    type: FieldType.TT_SELECT,
    requireNext: false,
    init: async () => {
      const res = await client.getJson("specimen");
      if (!res?.ok) return false;
      options = res.data.map((data: any) => ({
        label: getDisplayName(data.name, data.preferred_name),
        value: data.id,
      }));
      return true;
    },
    config: {
      showKeyboard: true,
    },
    options: () => options,
    validation: (val: Option) => Validation.required(val),
  };
};

const priorityField = () => {
  let options: Option[] = [];
  return {
    id: "priority",
    helpText: "Specify priority",
    type: FieldType.TT_SELECT,
    computedValue: (val: Option) => {
      return {
        order: {
          priority: val.value,
        },
      };
    },
    validation: (val: Option) => Validation.required(val),
    init: async () => {
      const res = await client.getJson("priorities");
      if (!res?.ok) return false;
      options = res.data.map((data: any) => ({
        label: data.name,
        value: data.id,
      }));
      return true;
    },
    options: () => options,
  };
};

const testTypeField = () => {
  let options: Option[] = [];
  let specimenID: number | null = null;
  return {
    id: "test_types",
    helpText: "Select test types",
    type: FieldType.TT_MULTIPLE_SELECT,
    config: {
      showKeyboard: true,
    },
    computedValue: (val: Option[], f: any) => {
      return {
        tests: val.map((v: Option) => {
          return {
            specimen: f.specimen_type.value,
            test_type: v.value,
          };
        }),
      };
    },
    options: async (f: any) => {
      const response = await client.getJson(`/clients/${route.params.client_id}`);
      const patientSex = response?.data?.sex.match(/f/i) ? "Female" : "Male"
      if (f.specimen_type.value != specimenID) {
        options = [];
        specimenID = f.specimen_type.value;
        const res: any = await client.getJson("specimen/test_types", {
          specimen_id: specimenID as number,
          sex: patientSex
        });
        if (!res?.ok) return [];
        options = res.data.map((testType: string) => ({
          label: testType,
          value: testType,
        }));
      }
      return options;
    },
    validation: (val: Option) => Validation.required(val),
  } as Field;
};

const timeSampleCollectedField = () => {
  return {
    id: "time_sample_collected",
    helpText: "Time Sample Collected",
    type: FieldType.TT_TIME,
    computedValue: (val: Option, f: any) => {
      return {
        order: {
          sample_collected_time: `${f.date} ${val.value}`,
        },
      };
    },
    validation: (val: Option, f: any) => {
      const requiredValidation = Validation.required(val);
      if (requiredValidation) return requiredValidation;

      const selectedDate = new Date(f.date);
      const today = new Date(HisDate.currentDate());

      selectedDate.setHours(0, 0, 0, 0);
      today.setHours(0, 0, 0, 0);

      if (selectedDate.getTime() === today.getTime()) {
        const timeValue = String(val.value);
        const [hours, minutes] = timeValue.split(':').map(Number);
        const selectedTime = new Date();
        selectedTime.setHours(hours, minutes, 0, 0);

        const now = new Date();

        if (selectedTime > now) {
          return ['Sample collection time cannot be in the future'];
        }
      }

      return null;
    },
  };
};

const fields = [
  visitTypeField(),
  requestingLocationField(),
  requestingClinicianField(),
  collectedByField(),
  specimenTypeField(),
  testTypeField(),
  dateField(),
  timeSampleCollectedField(),
  priorityField(),
];

const onFinish = async (_: any, data: any) => {
  const printService = new PrintService();
  const { getLocation, token } = Client();
  const userData = token.value?.authorization?.user;
  const labLocationID = userData.lab_locations.filter(
    (location: LabLocation) => location.name == getLocation()
  );

  const payload = {
    client: {
      id: parseInt(`${route.params.client_id}`),
    },
    lab_location: labLocationID[0].id,
    ...groupByTag(data, "encounter"),
    ...groupByTag(data, "order"),
    ...groupByTag(data, "tests"),
  };
  isFinishDisabled.value = true;
  client
    .postJson("orders", payload)
    .then((res: any) => {
      details.value = res.data;

      const accession_number = res.data.accession_number;

      if (!res.ok) return toastWarning(res.error);

      printService.writeLbl(
        "printout/accession_number",
        { accession_number },
        `${accession_number}.lbl`
      );
      isFinishDisabled.value = false;
      modalOpen.value = true;
      try {
        router.push(`/tests`);
      } catch (error) {
        console.log(error);
      }

      toastSuccess("Order as been created!");
      handleModal();
    })
    .catch(() => {
      isFinishDisabled.value = false;
      toastDanger(ClientError.GENERAL_ERROR)
    });
};
</script>
